#headerItem{
    height: 4.25rem;
    text-align: center;
    margin-bottom: 2em;
}

.AboutMeGridContainer{
    max-width: 1000px;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    grid-gap: 2em;
    margin-left: auto;
    margin-right: auto;
}

.item{
    -webkit-box-shadow: 5px 5px 15px rgba(0, 0, 0, 0.5),
                -5px -5px 15px rgba(255, 255, 255, 0.25);
    -moz-box-shadow: 5px 5px 15px rgba(0, 0, 0, 0.5),
                -5px -5px 15px rgba(255, 255, 255, 0.25);
    box-shadow: 5px 5px 15px rgba(0, 0, 0, 0.5),
                -5px -5px 15px rgba(255, 255, 255, 0.25);
}

.LinkButtonAboutMe{
    margin-top: 0rem !important;
}

.JoshImage{
    width: 100%;
    padding-top: 1rem;
    padding-bottom: 1rem;
    padding-left: 1rem;
    padding-right: 1rem;
}

.aboutMeText{
    font-size: 1.2rem;
    text-indent: 50px;
    margin-bottom: 2rem !important;
    padding-top: 1rem !important;
    padding-bottom: 1rem !important;
    padding-left: 1rem !important;
    padding-right: 1rem !important;
}

.aboutMeTextSmall{
    clear: both;
    text-align: center;
    font-weight: 500;
}

h1{
    padding-top: 0.4rem;
}

.imageEffect{
    position: relative;
    display: block;
    overflow: hidden;
}

.imageEffect::after{
    content: '';
    position: absolute;
    top: 0;
    left: -100%;
    width: 100%;
    height: 100%;
    background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.4), transparent);
    transition: 0.5s;
}

.imageEffect:hover::after{
    left: 100%;
}

@media (min-width: 0px){
    .AboutMeGridContainer{
        margin-left: 2rem;
        margin-right: 2rem;
    }

    .gridContainerSkills{
        margin-left: 2rem;
        margin-right: 2rem;
    }
}

@media (min-width: 1065px){
    .AboutMeGridContainer{
        margin-left: auto;
        margin-right: auto;
    }

    .gridContainerSkills{
        margin-left: auto;
        margin-right: auto;
    }
}